<template>
    <div id="app">
        <header class="header">
            <router-link class="header-left" to="/">
                <svg class="header-left-icon">
                    <use xlink:href="#icon-logo"></use>
                </svg>
            </router-link>
            <div class="header-right">
                <svg @click="loginDialogShow = true" v-if="JSON.stringify(this.$store.state.user)==='{}'" width="100%"
                     height="100%"
                     fill="currentColor">
                    <use xlink:href="#icon-login"></use>
                </svg>
                <svg @click="logoutDialogShow = true" v-else width="100%" height="100%"
                     fill="currentColor">
                    <use xlink:href="#icon-logout"></use>
                </svg>
            </div>
        </header>
        <keep-alive>
            <router-view/>
        </keep-alive>
        <nav class="nav">
            <svg class="nav-icon" :class="{'nav-icon-active': this.$route.name === 'read'}" @click="clickNav('read')">
                <use xlink:href="#icon-readme"></use>
            </svg>
            <svg class="nav-icon"
                 :class="{'nav-icon-active': this.$route.name === 'home' || this.$route.name === 'detail'}"
                 @click="clickNav('/')">
                <use xlink:href="#icon-home"></use>
            </svg>
            <svg class="btn-add" @click="clickAdd">
                <use xlink:href="#icon-add"></use>
            </svg>
            <svg class="nav-icon" :class="{'nav-icon-active': this.$route.name === 'repo'}">
                <use xlink:href="#icon-repo"></use>
            </svg>
            <svg class="nav-icon" :class="{'nav-icon-active': this.$route.name === 'user'}" @click="clickNav('user')">
                <use xlink:href="#icon-user"></use>
            </svg>
        </nav>
        <my-dialog v-model="loginDialogShow" :before-close="beforeLoginDialogClose"
                   :confirm-text="loginDialogConfirmText">
            <svg class="dialog-header-logo" slot="header">
                <use xlink:href="#icon-login"></use>
            </svg>
            <span slot="header">绑定 GitHub 信息</span>
            <my-input class="login-input" v-model="usernameValue" :label="usernameLabel"
                      :placeholder="usernamePlaceholder" ref="usernameInputRef"></my-input>
            <my-input class="login-input" type="password" v-model="tokenValue" :label="passwordLabel"
                      :placeholder="passwordPlaceholder" ref="passwordInputRef"></my-input>
        </my-dialog>
        <my-dialog v-model="logoutDialogShow" @on-confirm="clickLogoutDialogConfirm">
            <svg class="dialog-header-logo" slot="header">
                <use xlink:href="#icon-logout"></use>
            </svg>
            <span slot="header">注销账号</span>
            <div class="logout-section">确定要注销吗？</div>
        </my-dialog>
        <my-dialog v-model="editDialogShow" :before-close="beforeEditDialogClose" :confirm-text="addDialogConfirmText">
            <svg class="dialog-header-logo" slot="header">
                <use xlink:href="#icon-add"></use>
            </svg>
            <span slot="header">发表博客</span>
            <my-input class="edit-header" v-model="desValue" :label="desLabel" :placeholder="desPlaceholder"
                      ref="desInputRef"></my-input>
            <my-textarea :label="detailLabel" v-model="detailValue" :placeholder="detailPlaceholder"
                         ref="detailTextareaRef"></my-textarea>
        </my-dialog>
        <!--<drawer v-model="drawerShow" :before-close="beforeDrawerClose" :confirm-text="drawerConfirmText">-->
        <!--<svg class="edit-icon" slot="title">-->
        <!--<use xlink:href="#icon-write"></use>-->
        <!--</svg>-->
        <!--<my-input class="edit-header" v-model="desValue" :label="desLabel" :placeholder="desPlaceholder" ref="desInputRef"></my-input>-->
