        <!--<div class="edit-section-left">-->
        <!--<img class="edit-avatar" :src="this.$store.state.user.avatar_url"/>-->
        <!--</div>-->
        <!--<div class="edit-section-right">-->
        <!--<p class="edit-author">{{this.$store.state.user.login}}</p>-->
        <!--<p class="edit-time"></p>-->
        <!--</div>-->
        <!--</div>-->
        <!--<div class="edit-footer">-->
        <!--<my-textarea :label="detailLabel" v-model="detailValue" :placeholder="detailPlaceholder" ref="detailTextareaRef"></my-textarea>-->
        <!--</div>-->
        <!--</drawer>-->
    </div>
</template>

<script>
  import Dialog from './components/Dialog'
  import Drawer from './components/Drawer'
  import Input from './components/Input'
  import Textarea from './components/Textarea'

  export default {
    components: {
      'my-dialog': Dialog,
      'my-input': Input,
      'my-textarea': Textarea,
      'drawer': Drawer
    },
    data() {
      return {
        loginDialogShow: false,
        logoutDialogShow: false,
        editDialogShow: false,
        // drawerShow: false,
        loginDialogConfirmText: '绑定',
        usernameLabel: '用户名',
        usernamePlaceholder: '请输入GitHub用户名',
        passwordLabel: 'Token',
        passwordPlaceholder: '请输入有效Token',
        usernameValue: 'Lkkkkkkg',
        tokenValue: '',
        desValue: '',
        detailValue: '',
        desLabel: '标题',
        detailLabel: '内容',
        desPlaceholder: '请输入标题',
        detailPlaceholder: '请输入内容',
        addDialogConfirmText: '发表',
      }
    },
    methods: {
      clickNav(to) {
        if (JSON.stringify(this.$store.state.user) === '{}') {
          this.$tips('请先绑定', 'wrong', 2000);
        } else {
          this.$router.push(to);
        }
      },
      clickAdd() {
        if (JSON.stringify(this.$store.state.user) === '{}') {
          this.$tips('请先绑定', 'wrong', 2000);
        } else {
          this.editDialogShow = true;
        }
      },
      clickLogoutDialogConfirm() {
        this.$store.commit('SET_USER', {})
        sessionStorage.removeItem("user");
        sessionStorage.removeItem("token");
        this.$tips('注销成功', 'correct', 2000);
        this.$router.push('/read')
      },
      beforeLoginDialogClose(action, done) {
        if (action === 'on-confirm') {
          if (!(this.usernameValue && this.tokenValue)) {
            done(false);
          } else {
            this.$tips('正在绑定', 'loading');
            this.$ajax(`https://api.github.com/user?access_token=${this.tokenValue}`).then((res) => {
              if (res.status === 200) {
                if (this.usernameValue === res.data.login) {
                  this.$tips('绑定成功', 'correct', 2000);
                  this.$store.commit('SET_USER', res.data);
                  this.$store.commit('SET_TOKEN', this.tokenValue);
                  sessionStorage.setItem("user", JSON.stringify(res.data));
                  sessionStorage.setItem("token", this.tokenValue);
                  this.$refs.usernameInputRef.doExit()
                  this.$refs.passwordInputRef.doExit()
                  this.$router.push('/');
                  done()
                } else {
                  this.$tips('用户名与Token不一致', 'wrong', 2000);
                  done(false)
                }
              }
            }).catch(() => {
              this.$tips('用户名与Token不一致', 'wrong', 2000);
              done(false)
            })
          }
        } else {
          this.$refs.usernameInputRef.doExit()
          this.$refs.passwordInputRef.doExit()
          done()
        }
      },
      beforeEditDialogClose(action, done) {
        if (action === 'on-confirm') {
          if (!(this.desValue && this.detailValue)) {
            done(false);
          } else {
            this.$tips('正在发表', 'loading');
            this.$ajax.post(`https://api.github.com/gists?access_token=${this.$store.state.token}`, {
              "description": this.desValue,
              "public": true,
              "files": {
                "blog": {
                  "content": this.detailValue
                }
              }
            }).then((res) => {
              this.$store.commit('ADD_BLOG', res.data)
              this.$store.commit('SET_DETAILS', {
                key: res.data.id,
                value: res.data
              });
              this.$tips('发表成功', 'correct', 2000);
              this.$refs.desInputRef.doExit()
              this.$refs.detailTextareaRef.doExit()
              done();
            })
          }
        } else {
          done();
        }
      }
    }
  }
</script>

